/*bolg*/
.blogNav a{
    text-decoration: none;
    color: #fff;
}
.blogNav .blogTopBar{
    display: flex;
    justify-content: space-between;
    align-content: center;
}
.blogNav .navBlogList{
    display: flex;
    justify-content: start;
}
.blogNav .navBlogList li {
    padding: 12px 24px;
}
.blogNav .navBlogList li:hover{
    background: #8a0500;
}
.blogNav .search a{
    content: "";
    display: block;
    text-indent: 101%;
    flex-wrap: nowrap;
    overflow: hidden;
    width: 29px;
    height: 29px;
    margin: 12px 0;
    background: url("https://hexschool.github.io/webLayoutTraining1st/week3/icon-search.svg") no-repeat center center;
}
.blog h2{
    position: relative;
}
.blog h2::before{
    content: "";
    display: inline-block;
    width: 5px;
    height: 70px;
    background: #9F1213;
    position: absolute;
    top: 0;
    left: -16px;
}
.blog ul.blogList li{
    display: flex;
    justify-content: space-between;
    margin-bottom: 60px;
}
.blog ul.blogList li .img{
    width: 57%;
    height: auto;
    margin-right: 30px;
}
.blog ul.blogList li .img img{
    width: 100%;
}
.blog ul.blogList li .text{
    width: 42%;
}
.blog ul.blogList li .text h3{
    font-size: 2rem;
    margin-bottom: 2px;
}
.blog ul.blogList li .text h4{
    display: flex;
    justify-content: space-between;
}
.blog ul.blogList li .text h4 span{
    opacity: 0.7;
}
.blog ul.blogList li .text p{
    font-size: 1.25rem;
}
.blog a.more{
    font-size: 2.25rem;
    line-height: 1.5;
    text-decoration: none;
    text-align: right;
    display: block;
}
.blog .page{
    margin-top: 36px;/*請問這個為何沒有反應?? ul.blogList也沒有反應*/
}
.blog li.blogItem:last-child{
    margin-bottom: 96px;
}
.blog .blogList .text p span{
    display: none;
}
@media (max-width: 768px) {
    .blog .h2{
        font-size: 3rem;
    }
    .blog h2.mb-5{
        margin-bottom: 40px;
    }
    .blog .blogList .text p span{
        display: block;
    }
    .blog a.more{
        display: none;
    }
    .blog ul.blogList li{
        margin-bottom: 24px;
    }
    .blog ul.blogList li .text h3{
        font-size: 1.25rem;
        margin-bottom: 0px;
    }
    .blog ul.blogList li .text h4{
        font-size: 1rem;
    }
    .blog ul.blogList li .text p{
        font-size: 0.87rem;
        position: relative;
    }
    .blog ul.blogList li .text h4.mb-3{
        margin-bottom: 32px;
    }
    .blog ul.blogList li .text p.mb-3{
        margin-bottom: unset;
    }
    .blog ul.blogList li .img{
        width: 49%;
    }
    .blog ul.blogList li .text{
        width: 48%;
    }
    .blog .modle-more{
        position: absolute;
        right: 64px;
        bottom: 0;
        font-weight: bold;
    }
    .blog li.blogItem:last-child{
        margin-bottom: 64px;
    }
}
@media (max-width: 375px) {
    .blogNav {
        display: none;
    }
    .blog .h2{
        font-size: 1.75rem;
    }
    .blog h2::before{
        height: 40px;
        left: -8px;
    }
    .blog h2.mb-5{
        margin-bottom: 28px;
    }
    .blog h2.ml-1{
        margin-left: 8px;
    }
    .blog ul.blogList li{
        flex-direction: column;
    }
    .blog ul.blogList li .img{
        width: 100%;
        margin-bottom: 8px;
    }
    .blog ul.blogList li .text{
        width: 100%;
    }
    .blog ul.blogList li .text h3{
        font-size: 1rem;
    }
    .blog ul.blogList li .text h4 ,.blog ul.blogList li .text h4 span{
        font-size: 0.87rem;
    }
    .blog ul.blogList li .text p{
        display: none;
    }
    .blog ul.blogList li .text h4.mb-3{
        margin-bottom: unset;
    }
    .blog ul.blogList li{
        margin-bottom: 20px;
    }
}