/*qa*/
.qa .title{
    font-size: 2rem;
}
.qa .qaList{
    width: 83%;
    margin-bottom: -32px;
}
.qa .qaList li{
    margin-bottom: 32px;
}
.qa .qaList li h5{
    font-weight: bold;
}
.qa .qaList li p{
    margin-left: 8.3%;
}
@media (max-width: 768px) {
    .qa .qaList{
        width: 100%;
        margin-bottom: -24px;
    }
    .qa .qaList li{
        margin-bottom: 24px;
    }
    .qa .qaList li p{
        margin-left: 8.6%;
    }
}
@media (max-width: 375px) {
    .qa .qaList{
        margin-bottom: -20px;
    }
    .qa .qaList li{
        margin-bottom: 20px;
    }
    .qa .qaList li p{
        margin-left: unset;
    }
    .qa .qaItem .mb-2{
        margin-bottom: 8px;
    }
}